
import {
JDGetPeasonalContactInfo,
JDGetPeasonalContactLetter
} from "@/api/getData";
import { mapState } from 'vuex'
import Popup from 'vux/src/components/popup/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import $ from "jquery";

export default {
data() {
return {
personalContactInfoModel: [],
personalContactInfoObject: {
contact_id: 758,
contact_name: "艾小兰",
email: "xiaolan@goldemperor.com",
group_name: "金帝人力资源课",
mobile_full: "13587421352",
mobile_short: "651352",
organize_name: "金帝集团股份有限公司(测试库)",
phone_full: "",
phone_short: "77767",
post_name: "绩效副课长",
prefixletter: "A"
},
letter: [],
keyWord: "",
arr: []
};
},
computed: {
...mapState({
userInfo: state => state.mutations.userInfo
})
},
directives: {},
components: {
Popup,
XButton
},
mounted() {

this.GetPeasonalContactInfo();
},
methods: {
toInfo(val) {
this.$router.push({
path: "/mailList/info",
query: {
contact_id: val
}
});
},
look() {
this.$router.push({ path: "/QRcode" });
},
log() {},
invitation() {
this.$router.push({ path: "/invitation" });
},
close() {},
init() {},
async GetPeasonalContactInfo() {

let data = await JDGetPeasonalContactInfo({
KeyWord: this.keyWord,

});


this.personalContactInfoModel = eval(data);


let letterA = await JDGetPeasonalContactLetter({


});

for (var i = 0; i < eval(letterA).length; i++) {
this.letter.push(eval(letterA)[i].letter);
}

this.$nextTick(() => {
this.init();
});
},
async changeKeyWord(Flag) {
let data = await JDGetPeasonalContactInfo({
KeyWord: this.keyWord,

});
console.info(eval(data).length);
if(eval(data).length>500){
this.$vux.toast.text('返回数据超过500个，请填写搜索条件');
return;
}
this.personalContactInfoModel = eval(data);
this.arr = [];
for (var i = 0; i < eval(data).length; i++) {
this.arr.push(eval(data)[i].prefixletter.toUpperCase());
}
this.letter = Array.from(new Set(this.arr));
this.$nextTick(() => {
this.init();
});
}
}
};



